import {request} from "../../request.js"
export default {
	data() {
		return {
			checked: true,
			amount:0,//订单总金额(未折扣)
			payprice:"",//需要支付的金额
			discountPrince:"",//折扣了的金额
			amountsPayable:0,//应付金额
			balance:"",//用户余额
			n_coupon:"",//用户折扣券
			n_money:"",//用户现金券
			use_balance:180,//用户使用的余额
			use_n_coupon:0,//用户使用的折扣券
			use_n_money:0,//用户使用的现金券
			shop_id:"",
			sum: '',//应付总额
			cash: '' ,//用户输入的现金券
			remaining: '' //用户输入的余额
		};
	},
	onLoad() {
		request({
			url:"index.php?s=/wap/order/payOfflineOrder",
		}).then(res=>{
			console.log(res);
			if(res.data.code == 200){
				let tmp = res.data;
				this.payprice = tmp.sumAmount;
				this.amount=tmp.amount;
				this.discountPrince = tmp.discountCoupon;
				this.balance=tmp.account.balance;
				this.n_coupon=tmp.account.n_coupon;
				this.n_money = tmp.account.n_money;
				this.shop_id=tmp.shop_id
				this.use_n_coupon=tmp.discountCoupon;
			}else{
				uni.showToast({
					title:"获取订单失败!",
					icon:"none",
				})
			}
		})
	},
	methods:{
		changeChecked() {
			this.checked = !this.checked
		},
		createOrderOffline(){
			request({
				url:"index.php?s=/wap/order/createOfflineOrder",
				data:{
					shop_id:this.shop_id, 			//店铺ID
					use_balance:this.remaining,				//使用的余额
					use_n_money:this.cash,				//使用的现金券
					use_n_coupon:this.use_n_coupon,	//使用的折扣券
					discount_account:this.payprice, //计算了折扣后的金额 就是后台返回来的金额
					payprice:this.amountsPayable,	//用户需要支付的金额   就是需要调用微信支付的金额
					amount:this.amount,				//订单的总金额  后台给你的
				},
				method:"POST"
			}).then(res => {
				console.log(res);
				let data = res.data.msg;
				if(res.data.code > 0) {
					uni.showToast({
						title: data,
						icon: 'none'
					})
					setTimeout(function(){
						uni.navigateTo({
							url:"/pages/components/order/order"
						})
					},1000);
				}else{
					uni.showToast({
						title:"data",
						icon:"none",
					})
				}
			})
		},
		// 现金券
		money(event) {
			this.cash = parseInt(event.detail.value) || 0;
			if(this.n_money == 0 || !this.n_money) {
				// uni的bug
				setTimeout(() => {this.cash = 0;},50)
				uni.showToast({
					title: '现金券为零',
					icon: 'none'
				})
				return
			}
			else {
			// 如果输入金额大于商品总额,显示商品总额
				// 如果用户存在现金券
				this.cash = parseInt(event.detail.value) || 0;
				// 当前应付总额
				let price = this.payprice;
				// 当前用户拥有的现金券
				let userCash = parseFloat(this.n_money);
				// 当前用户输入的现金券和余额的总和
				let sum = this.cash + (parseInt(this.remaining) ? parseInt(this.remaining): 0);
				// 支付总额大于用户现有现金券,默认最大输入位当前现金券(this.n_money)
				if(this.remaining == '') {
					// 如果用户现金券小于当前的支付金额
					if(userCash < price) {
						// 输入现金券大于用户的现金券
						if(this.cash > userCash) {
							setTimeout(() => {this.cash = userCash;this.sum = (price - this.cash).toFixed(2)},50)
						}
						else {
							this.sum = (price - this.cash).toFixed(2);
						}
					}
					// 如果用户现金券大于当前的支付金额
					else {
						// 输入现金券大于当前的总价
						if(this.cash > price) {
							setTimeout(() =>{this.cash = price},50)
							this.sum = 0
						}
						else {
							this.sum = (price - this.cash).toFixed(2)
						}
					}
				}
				// 如果用户输入的余额不为空
				else if(this.remaining != '') {
					// 如果用户拥有的现金券大于当前商品的总价
					if(userCash > price) {
						// 当前余额+现金券大于支付总额
						if(sum >= price) {
							// 输入的现金券小于当前支付总额
							if(this.cash < price) {
								setTimeout(()=> {this.remaining = (price - this.cash).toFixed(2);},50)
								this.sum = 0;
							}
							// 输入的现金券大于当前商品价格
							else {
								setTimeout(()=> {this.cash = price;this.remaining = 0},50)
								this.sum = 0;
							}
						}
						else {
							this.sum = (price - this.remaining - this.cash).toFixed(2)
						}
					}
					// 如果用户拥有的现金券小于当前商品的总价
					else {
						if(sum >= price) {
							// 如果输入的现金券大于用户的现金券
							if(this.cash > userCash) {
								setTimeout(()=> {this.cash = userCash;this.sum = (price - this.remaining - userCash).toFixed(2)},50)
							}
							else {
								this.sum = (price - this.remaining - this.cash).toFixed(2);
							}
						}
						else {
							this.sum = (price - this.remaining - this.cash).toFixed(2);
						}
					}
				}
			}
		},
		// 余额
		changeBalance(event) {
			// 如果用户存在现金券
			this.remaining = parseInt(event.target.value) || 0;
			// 当前要支付的总价
			let price = this.payprice;
			// 当前用户拥有的余额
			let userBalance = parseFloat(this.balance);
			// 当前用户输入的现金券和余额的总和
			let sum = this.remaining + (parseInt(this.cash) ? parseInt(this.cash): 0);
			// 如果现金券等于价格，默认给0
			if(this.cash == price ) {
				setTimeout(()=> {this.remaining = 0;},50)
			}
			// 如果用户没有输入现金券
			if(this.cash =='' || this.cash == 0) {
				// 如果余额存在且余额大于支付总额
				if(userBalance && userBalance >= price) {
					if(this.remaining > price) {
						setTimeout(()=> {this.remaining = price;},50);
						this.sum = 0
					}
					else {
						this.sum = (price - this.remaining).toFixed(2)
					}
				}
				// 余额存在且余额小于支付总额
				else {
					if(this.remaining > userBalance) {
						setTimeout(()=> {this.remaining = userBalance;this.sum = (price - this.remaining).toFixed(2)},50)
					}
					else {
						this.sum = (price - this.remaining).toFixed(2)
					}
				}
			}
			// 用户输入了现金券
			else {
				// 如果余额存在且余额大于支付总额
				if(userBalance && userBalance >= price) {
					if(this.remaining > price - this.cash) {
						setTimeout(()=> {this.remaining = (price - this.cash).toFixed(2)})
						this.sum = 0;
					}
					else {
						this.sum = (price - this.remaining - this.cash).toFixed(2);
					}
				}
				// 余额小于支付总额
				else {
					if(this.remaining > userBalance) {
						setTimeout(
						()=> {this.remaining = userBalance;this.sum = (price - this.remaining - this.cash).toFixed(2)},50)
					}
					else {
						this.sum = (price - this.remaining - this.cash).toFixed(2)
					}
				}
			}
			
		},
		
		
		
	},
}
